import { Component, OnInit, Input } from '@angular/core'

@Component({
  selector: 'app-card-beneficio',
  templateUrl: './card-beneficio.component.html',
  styleUrls: ['./card-beneficio.component.css']
})
export class CardBeneficioComponent implements OnInit {

  private _benTipo = ''
  private _benNombre = ''
  private _benDisponible = ''
  private _benIconName = "far fa-chart-bar"

  @Input()
  get benTipo(): string {
    return this._benTipo
  }

  set benTipo(benTipo: string) {
    this._benTipo = benTipo === undefined ? '' : benTipo
  }

  @Input()
  get benNombre(): string {
    return this._benNombre
  }

  set benNombre(benNombre: string) {
    this._benNombre = benNombre === undefined ? '' : benNombre
  }

  @Input()
  get benDisponible(): string {
    return this._benDisponible
  }

  set benDisponible(benDisponible: string) {
    this._benDisponible =
      benDisponible === undefined ? '' : benDisponible
  }

  @Input()
  get benIconName(): string {
    return this._benIconName;
  }
  set benIconName(benIconName: string) {
    this._benIconName =
      benIconName === undefined ? "far fa-chart-bar" : benIconName;
  }

  constructor() {
  }

  ngOnInit(): void {
  }
}
